import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminIFXEnvironmentComponent } from './admin-ifxEnvironment.component';

const routes: Routes = [
  {
    path: 'duplicate/:ifxEnvironmentId',
    component: AdminIFXEnvironmentComponent,
    data: { type: 'duplicate' },
    pathMatch: 'full',
  },
  {
    path: 'create',
    component: AdminIFXEnvironmentComponent,
    data: { type: 'create' },
    pathMatch: 'full',
  },
  {
    path: ':ifxEnvironmentId',
    component: AdminIFXEnvironmentComponent,
    data: { type: 'properties' },
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminIFXEnvironmentRoutingModule {}
