import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Data, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import { Subscription, combineLatest } from 'rxjs';
import { FieldDefinition, MetaDataType, Tab } from 'shared-code-lib';
import { Log } from '../../blocks/core/log';
import { IFXEnvironment, IIFXEnvironment, Variables } from '../../blocks/models/deployments';
import { ErrorsService } from '../../blocks/services/errors.service';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { ApiResult, Control } from '../../blocks/services/models';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';

@Component({
  selector: 'app-admin-ifxEnvironment',
  templateUrl: './admin-ifxEnvironment.component.html',
  styleUrls: ['./admin-ifxEnvironment.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminIFXEnvironmentComponent implements OnInit, OnDestroy {
  rightControls: Array<Control> = [];

  routeParams: Params;
  routeData: Data;

  saveButton: Control;
  cancelButton: Control;
  ifxEnvironment: IFXEnvironment;

  fieldName: FieldDefinition;
  tabs = [];

  activeTab = 'variables';

  components = [];

  subscriptions = new Subscription();

  constructor(
    private translate: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private hydraApi: HydraApiService,
    private toastr: ToastrService,
    private errorsService: ErrorsService,
    private spinner: SpinnerStateService,
    public ngxSmartModalService: NgxSmartModalService,
    private cdr: ChangeDetectorRef
  ) {}

  ngOnInit() {
    this.createTabs();

    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.routeData = data;
        if (this.routeData.type == 'create') {
          this.ifxEnvironment = new IFXEnvironment({
            name: '',
            variables: new Variables(),
          } as IIFXEnvironment);
        } else {
          this.loadItems();
        }
      })
    );

    this.createButtons();
    this.createFields();
  }

  private createTabs() {
    this.tabs.push(new Tab('variables', this.translate.instant('admin-ifxEnvironment.variables')));
    this.tabs.push(new Tab('roles', this.translate.instant('admin-ifxEnvironment.roles')));
  }

  private createButtons = () => {
    this.saveButton = new Control(this.onSave, 'Save', 'btn btn-primary btn-minwidth', '', '', 'btn', false);
    this.cancelButton = new Control(this.onCancel, 'Cancel', 'btn btn-light btn-minwidth', '', '', 'btn', false);
    this.rightControls.push(this.saveButton);
    this.rightControls.push(this.cancelButton);

    this.saveButton.setTranslation(this.translate.instant('Common.Save'));
    this.cancelButton.setTranslation(this.translate.instant('Common.Cancel'));
  };

  private createFields = () => {
    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-ifxEnvironment.name'),
      placeholder: this.translate.instant('admin-ifxEnvironment.name-placeholder'),
      required: true,
    });
  };

  CreateField = (label: string, required: boolean = false): FieldDefinition => {
    return new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant(label),
      placeholder: this.translate.instant(label),
      required: required,
    });
  };

  onSave = () => {
    if (this.ifxEnvironment.id) {
      this.hydraApi.updateIFXEnvironment(this.ifxEnvironment, this.ifxEnvironmentSaved, this.ifxEnvironmentSaveFailed);
    } else {
      this.hydraApi.createIFXEnvironment(this.ifxEnvironment, this.ifxEnvironmentSaved, this.ifxEnvironmentSaveFailed);
    }

    this.spinner.showSavingSpinner();
  };

  ifxEnvironmentSaved = (result: ApiResult) => {
    if (result.success) {
      this.toastr.success(this.translate.instant('admin-ifxEnvironment.saved'));
      this.returnToGrid();
    } else {
      this.errorsService.showErrorsModal(result.errors, 'admin-ifxEnvironment.validation-errors');
    }
    this.spinner.hide();
  };

  ifxEnvironmentSaveFailed = (error: any) => {
    this.toastr.error(this.translate.instant('admin-ifxEnvironment.save-failed'));
    Log.error('admin-ifxEnvironment.save-failed', error);
    this.spinner.hide();
  };

  onCancel = () => {
    this.returnToGrid();
  };

  returnToGrid = () => {
    this.router.navigateByUrl(
      'product/settings/BambaDWFToolsApplication/BambaDWFToolsApplication-Upland.Bamba.DWFTools.IFXReleaseManager.IFXEnvironments.Folder'
    );
  };

  ifxEnvironmentLoaded = (ApiResult: any): void => {
    if (this.routeData.type === 'duplicate') {
      ApiResult.object.name = `${this.translate.instant('Admin.CopyOf')} ${ApiResult.object.name}`;
      ApiResult.object.id = '';
    }
    this.ifxEnvironment = new IFXEnvironment({ ...ApiResult.object });
    this.cdr.markForCheck();
    this.spinner.hide();
  };

  ifxEnvironmentFailed = (error: any): void => {
    this.toastr.error(this.translate.instant('admin-ifxEnvironment.load-failed'));
    Log.error('admin-ifxEnvironment.load-failed', error);
    this.spinner.hide();
  };

  loadItems = (): void => {
    if (this.routeData.type === 'properties' || this.routeData.type === 'duplicate') {
      this.spinner.show(this.translate.instant('admin-ifxEnvironment.loading-ifxEnvironment'), document.body);
      this.hydraApi.getIFXEnvironment(
        this.routeParams.ifxEnvironmentId,
        this.ifxEnvironmentLoaded,
        this.ifxEnvironmentFailed
      );
    }
  };

  activateTab = (view) => {
    this.activeTab = view;
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
