import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { Role } from 'shared-code-lib';
import { IFXEnvironment } from '../../../blocks/models/deployments';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';

@Component({
  selector: 'app-properties-roles',
  templateUrl: './properties-roles.component.html',
  styleUrls: ['./properties-roles.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class PropertiesRolesComponent implements OnInit {
  @Input() ifxEnvironment: IFXEnvironment;
  @Output() roleIdsChange = new EventEmitter<Array<string>>();

  available: Array<Role> = [];
  current: Array<Role> = [];

  constructor(private hydraApi: HydraApiService) {}

  ngOnInit() {
    this.loadPicklistItems();
  }

  currentChanged = (items: Array<Role>) => {
    this.ifxEnvironment.roleIds = [];
    for (let item of items) {
      this.ifxEnvironment.roleIds.push(item.id);
    }

    this.roleIdsChange.emit(this.ifxEnvironment.roleIds);
  };

  loadPicklistItems = () => {
    this.hydraApi.getRoles(this.loaded, this.loadFailed);
  };

  loaded = (roles: Array<Role>) => {
    this.available = roles;
    this.current = roles.filter((r) => this.ifxEnvironment.roleIds.find((ri) => ri == r.id) != null);
  };

  loadFailed = () => {};
}
